.c-hola {
  @include font-family(heading);
  font-weight: 900;
  text-transform: uppercase;
}

  .c-hola__text {
    position: absolute;
    text-indent: -9999rem;

    @media only screen and (min-width: rem(420)) {
      position: static;
      text-indent: 0;
    }
  }

  .c-hola__ho {
    font-size: rem(25);
  }

  .c-hola__svg {
    width: 100%;

    @media only screen and (min-width: rem(420)) {
      display: none;
    }
  }

    .c-hola__la {
      font-size: rem(26px);
    }
