/*------------------------------------*\
  #LINKS
\*------------------------------------*/

/**
 * Default styles for simple hyperlinks.
 */

a {
  color: $color-links;
  text-decoration-thickness: rem(2);
  transition: all .2s;

  @include hocus() {
    text-decoration-thickness: rem(3);
    text-decoration-color: hsl(0, 56.3%, 50%);
  }
}
