  --text-lg: calc(var(--text-md) * var(--text-scale-ratio) + .3vw);
  --text-xl: calc(var(--text-lg) * var(--text-scale-ratio) + .3vw);
  --text-xxl: calc(var(--text-xl) * var(--text-scale-ratio) + .3vw);
