///*----------------------------------*\
//  #TYPOGRAPHY
//\*----------------------------------*/

// Mixins to help generating typographical rules.

// Shorthand onto specific font-stacks.
@mixin font-family($family) {

  @if ($family == "heading") {
    font-family: 'Hepta Slab', sans-serif;
  }

  @elseif ($family == "body") {
    font-family: "Work Sans", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
  }

  @else {
    @warn "#{$family} is not a valid font-family.";
  }

}

// Line-height Crop
@mixin lhCrop($line-height) {
  &::before {
    content: '';
    display: block;
    height: 0;
    width: 0;
    margin-top: calc((1 - #{$line-height}) * 0.5em);
  }
}


:root {

  // body font size
  --text-base-size: 1em;

  // type scale
   --text-scale-ratio: 1.2;
   --text-xs: calc((1em / var(--text-scale-ratio)) / var(--text-scale-ratio) + .3vw);
   --text-sm: calc(var(--text-xs) * var(--text-scale-ratio) + .3vw);
   --text-md: calc(var(--text-sm) * var(--text-scale-ratio) * var(--text-scale-ratio) + .3vw);
   --text-lg: calc(var(--text-md) * var(--text-scale-ratio) + .3vw);
   --text-xl: calc(var(--text-lg) * var(--text-scale-ratio) + .3vw);
   --text-xxl: calc(var(--text-xl) * var(--text-scale-ratio) + .3vw);
   --text-xxxl: calc(var(--text-xxl) * var(--text-scale-ratio) + .3vw);
}



@mixin text-preset-1 {
    font-size: var(--text-xxxl);
    @include font-family(heading);
    font-weight: 900;
    line-height: 1.2;
}

@mixin text-preset-2 {
    font-size: var(--text-xxl);
    @include font-family(heading);
    font-weight: 900;
    line-height: 1.2;
}

@mixin text-preset-3 {
    font-size: var(--text-xl);
    @include font-family(heading);
    font-weight: 800;
    line-height: 1.2;
    @include lhCrop(1.2);
}

@mixin text-preset-4 {
    font-size: var(--text-lg);
    @include font-family(heading);
    font-weight: 700;
    line-height: 1.2;
}

@mixin text-preset-4--sm {
    font-size: var(--text-md);
    @include font-family(heading);
    font-weight: 700;
    line-height: 1.2;
}

@mixin text-preset-5 {
    font-size: var(--text-sm);
    @include font-family(body);
    font-weight: 300;
    text-transform: uppercase;
    line-height: 1.2;
}

@mixin text-preset-6 {
    font-size: var(--text-xs);
    @include font-family(body);
    font-weight: 800;
    line-height: 1.2;
}

@mixin text-preset-7 {
    font-size: var(--text-sm);
    @include font-family(body);
    line-height: 1.4;
}

@mixin text-preset-8 {
    font-size: var(--text-xs);
    @include font-family(body);
    line-height: 1.5;
}
