/* Load the variable fonts */
@font-face {
	font-family: 'Hepta Slab';
	src: url('../fonts/HeptaSlab-VF.woff2') format('woff2-variations'),
			 // will be the standard and works in Safari now
			 url('../fonts/HeptaSlab-VF.woff2') format('woff2');
			 // for the other supporting browsers
  font-weight: 1 999;
	font-style: normal;
	font-display: fallback;
}

@font-face {
	font-family: 'Work Sans';
	src: url('../fonts/WorkSans-Roman-VF.woff2') format('woff2-variations'),
			 // will be the standard and works in Safari now
			 url('../fonts/WorkSans-Roman-VF.woff2') format('woff2');
			 // for the other supporting browsers
  font-weight: 1 999;
	font-style: normal;
	font-display: fallback;
}

@font-face {
	font-family: 'Work Sans';
	src: url('../fonts/WorkSans-Italic-VF.woff2') format('woff2-variations'),
			 // will be the standard and works in Safari now
			 url('../fonts/WorkSans-Italic-VF.woff2') format('woff2');
			 // for the other supporting browsers
  font-weight: 1 999;
	font-style: italic;
	font-display: fallback;
}


/* Load the static fonts as a fallback */
@font-face {
