.c-project-list {
  -webkit-overflow-scrolling: touch;
  scroll-padding: 0 var(--space-md);
  display: flex;
  padding-bottom: var(--space-md);
  overflow: auto;
  list-style: none;
  scroll-snap-type: x mandatory;

  &::after {
    flex: 0 0 var(--space-md);
    content: '';
  }
}
  .c-project-list__item {
    scroll-snap-align: start;
    position: relative;
    margin-left: var(--space-md);
  }

    .c-project-list__link {
      display: block;
      min-width: 70vw;
      margin-top: rem(80);
      padding: rem(80) var(--space-md) var(--space-md);
      border-radius: var(--global-radius);
      background-color: var(--project-bg);
      color: var(--project-color);
      text-decoration: none;

      @include hocus() {
        text-decoration: none;
      }
    }

      .c-project-list__client {
        @include text-preset-2;
      }

      .c-project-list__type {
        @include text-preset-4--sm;
        font-weight: 400;
      }

      .c-project-list__img {
        position: absolute;
        top: 0;
        left: 0;
      }


.c-stream-list {
  grid-gap: rem(8);
  display: grid;
  grid-auto-flow: dense;
  grid-template-columns: repeat(6, 1fr);

  @media only screen and (min-width: rem(420)) {
    grid-template-columns: repeat(8, 1fr);
  }
}

.c-stream-list__link {
  display: block;
  width: 100%;
  min-width: 5vh;
  height: 100%;
  min-height: 5vh;
  background-color: pink;
}
